          num1:1,
          num2:1,
          switch:true
          
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄',
           num1:1,
          num2:1,
          switch:true
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄',
           num1:1,
          num2:1,
          switch:true
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
           num1:1,
          num2:1,
          switch:false
        }],
          currentPage1: 5,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        editShow:false,
        tabName:'all',
        activeName: 'basic',
        ruleForm: {
          name: '',
          nickname: '',
          tel: '',
          resource: '',
         
        },
        rules: {
          name: [
            { required: true, message: '请输入真实姓名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          tel: [
            { required: true, message: '请输入电话号码', trigger: 'change' }
          ],
          nickname: [
            { required: true, message: '请输入微信号码', trigger: 'change' }
          ],
