        options:[{ value: '选项1',
                    label: '分类1'
                    }, {
                    value: '选项2',
                    label: '分类2'
                    }, {
                    value: '选项3',
                    label: '分类3'
                    }, {
                    value: '选项4',
                label: '分类4'}],
                checkList:["热卖"],
